"use server"

import { createClient } from "@/lib/supabase/server"
import { revalidatePath } from "next/cache"
import * as XLSX from "xlsx"

interface ImportResult {
  success: boolean
  total: number
  imported: number
  skipped: number
  errors: string[]
}

/**
 * Import chm cng t file Excel
 * Format file:
 * | M N.Vin | Tn nhn vin | Phng ban | Ngy | Th | Vo | Ra |
 * | NV001     | Nguyn Vn A  | IT        | 01   | T2  | 08:30 | 17:30 |
 */
export async function importAttendanceFromExcel(
  formData: FormData
): Promise<ImportResult> {
  const supabase = await createClient()

  const file = formData.get("file") as File
  const monthYear = formData.get("monthYear") as string // Format: "2025-11"

  if (!file) {
    return { success: false, total: 0, imported: 0, skipped: 0, errors: ["Khng c file"] }
  }

  if (!monthYear) {
    return { success: false, total: 0, imported: 0, skipped: 0, errors: ["Cha chn thng/nm"] }
  }

  const [year, month] = monthYear.split("-").map(Number)

  try {
    // c file Excel
    const buffer = await file.arrayBuffer()
    const workbook = XLSX.read(buffer, { type: "array" })
    const sheetName = workbook.SheetNames[0]
    const worksheet = workbook.Sheets[sheetName]

    // Chuyn i sang JSON
    const rawData = XLSX.utils.sheet_to_json(worksheet, { header: 1 }) as unknown[][]

    if (rawData.length < 2) {
      return { success: false, total: 0, imported: 0, skipped: 0, errors: ["File rng hoc khng c d liu"] }
    }

    // B header row
    const dataRows = rawData.slice(1)
    const errors: string[] = []
    let imported = 0
    let skipped = 0

    // Ly danh sch employee  map employee_code -> id
    const { data: employees } = await supabase
      .from("employees")
      .select("id, employee_code")

    const employeeMap = new Map(
      employees?.map((e) => [e.employee_code?.toLowerCase().trim(), e.id]) || []
    )

    for (let i = 0; i < dataRows.length; i++) {
      const row = dataRows[i]
      const rowNum = i + 2 // +2 v b header v index t 0

      // Kim tra row c  d liu khng
      if (!row || row.length < 6) {
        skipped++
        continue
      }

      // Format: M N.Vin | Tn nhn vin | Phng ban | Ngy | Th | Vo | Ra
      const employeeCode = String(row[0] || "").trim().toLowerCase()
      // row[1] = Tn nhn vin (b qua, dng  i chiu)
      // row[2] = Phng ban (b qua)
      const dayValue = row[3] // Ngy trong thng (1-31)
      // row[4] = Th (b qua)
      const checkInValue = row[5] // Gi vo
      const checkOutValue = row[6] // Gi ra

      // Validate employee_code
      if (!employeeCode) {
        skipped++
        continue
      }

      const employeeId = employeeMap.get(employeeCode)
      if (!employeeId) {
        errors.push(`Dng ${rowNum}: Khng tm thy nhn vin "${row[0]}"`)
        skipped++
        continue
      }

      // Parse ngy
      let day: number
      if (typeof dayValue === "number") {
        day = Math.floor(dayValue)
      } else {
        day = parseInt(String(dayValue || "").trim(), 10)
      }

      if (isNaN(day) || day < 1 || day > 31) {
        errors.push(`Dng ${rowNum}: Ngy khng hp l "${dayValue}"`)
        skipped++
        continue
      }

      // To date string
      const dateStr = `${year}-${String(month).padStart(2, "0")}-${String(day).padStart(2, "0")}`

      // Validate ngy hp l trong thng
      const testDate = new Date(year, month - 1, day)
      if (testDate.getMonth() !== month - 1) {
        errors.push(`Dng ${rowNum}: Ngy ${day} khng tn ti trong thng ${month}`)
        skipped++
        continue
      }

      // Parse gi vo
      const checkInTime = parseTimeValue(checkInValue)
      if (!checkInTime) {
        // Khng c gi vo th b qua dng ny
        skipped++
        continue
      }

      // Parse gi ra (optional)
      const checkOutTime = parseTimeValue(checkOutValue)

      // To timestamp vi mi gi VN (+07:00)
      const checkInTimestamp = `${dateStr}T${checkInTime}:00+07:00`
      const checkOutTimestamp = checkOutTime ? `${dateStr}T${checkOutTime}:00+07:00` : null

      // Kim tra  c record cho ngy ny cha
      const { data: existing } = await supabase
        .from("attendance_logs")
        .select("id")
        .eq("employee_id", employeeId)
        .gte("check_in", `${dateStr}T00:00:00`)
        .lt("check_in", `${dateStr}T23:59:59`)
        .single()

      if (existing) {
        // Update record hin c
        const { error } = await supabase
          .from("attendance_logs")
          .update({
            check_in: checkInTimestamp,
            check_out: checkOutTimestamp,
            source: "import",
          })
          .eq("id", existing.id)

        if (error) {
          errors.push(`Dng ${rowNum}: Li cp nht - ${error.message}`)
          skipped++
        } else {
          imported++
        }
      } else {
        // Insert record mi
        const { error } = await supabase.from("attendance_logs").insert({
          employee_id: employeeId,
          check_in: checkInTimestamp,
          check_out: checkOutTimestamp,
          source: "import",
        })

        if (error) {
          errors.push(`Dng ${rowNum}: Li thm mi - ${error.message}`)
          skipped++
        } else {
          imported++
        }
      }
    }

    revalidatePath("/dashboard/attendance")
    revalidatePath("/dashboard/attendance-management")

    return {
      success: true,
      total: dataRows.length,
      imported,
      skipped,
      errors: errors.slice(0, 10), // Gii hn 10 li u
    }
  } catch (error) {
    console.error("Error importing attendance:", error)
    return {
      success: false,
      total: 0,
      imported: 0,
      skipped: 0,
      errors: [`Li c file: ${error instanceof Error ? error.message : "Unknown error"}`],
    }
  }
}

/**
 * Parse gi tr thi gian t Excel
 * H tr: "08:30", "8:30", 0.354166... (Excel time fraction)
 */
function parseTimeValue(value: unknown): string | null {
  if (value === null || value === undefined || value === "") {
    return null
  }

  if (typeof value === "number") {
    // Excel time fraction (0.0 = 00:00, 0.5 = 12:00, 1.0 = 24:00)
    const totalMinutes = Math.round(value * 24 * 60)
    const hours = Math.floor(totalMinutes / 60) % 24
    const minutes = totalMinutes % 60
    return `${String(hours).padStart(2, "0")}:${String(minutes).padStart(2, "0")}`
  }

  const timeStr = String(value).trim()
  
  // Match HH:mm or H:mm format
  const match = timeStr.match(/^(\d{1,2}):(\d{2})(?::\d{2})?$/)
  if (match) {
    const hours = parseInt(match[1], 10)
    const minutes = parseInt(match[2], 10)
    if (hours >= 0 && hours < 24 && minutes >= 0 && minutes < 60) {
      return `${String(hours).padStart(2, "0")}:${String(minutes).padStart(2, "0")}`
    }
  }

  return null
}

/**
 * To file Excel mu  download
 */
export async function generateAttendanceTemplate(): Promise<{
  success: boolean
  data?: string
  error?: string
}> {
  try {
    const supabase = await createClient()

    // Ly danh sch nhn vin active vi phng ban
    const { data: employees } = await supabase
      .from("employees")
      .select("employee_code, full_name, departments(name)")
      .eq("status", "active")
      .order("employee_code")

    // To workbook
    const wb = XLSX.utils.book_new()

    // Sheet 1: Template vi format mi
    const templateData = [
      ["M N.Vin", "Tn nhn vin", "Phng ban", "Ngy", "Th", "Vo", "Ra"],
      ["NV001", "Nguyn Vn A", "IT", 1, "T2", "08:30", "17:30"],
      ["NV001", "Nguyn Vn A", "IT", 2, "T3", "08:45", "17:45"],
      ["NV002", "Trn Th B", "HR", 1, "T2", "08:30", "17:30"],
    ]
    const ws1 = XLSX.utils.aoa_to_sheet(templateData)

    // Set column widths
    ws1["!cols"] = [
      { wch: 12 }, // M N.Vin
      { wch: 20 }, // Tn nhn vin
      { wch: 15 }, // Phng ban
      { wch: 6 },  // Ngy
      { wch: 5 },  // Th
      { wch: 8 },  // Vo
      { wch: 8 },  // Ra
    ]

    XLSX.utils.book_append_sheet(wb, ws1, "Chm cng")

    // Sheet 2: Danh sch nhn vin
    const employeeData: (string | null | undefined)[][] = [
      ["M N.Vin", "Tn nhn vin", "Phng ban"],
      ...(employees?.map((e) => {
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        const dept = (e as any).departments as { name: string } | null
        return [e.employee_code, e.full_name, dept?.name || ""]
      }) || []),
    ]
    const ws2 = XLSX.utils.aoa_to_sheet(employeeData)
    ws2["!cols"] = [{ wch: 12 }, { wch: 25 }, { wch: 20 }]
    XLSX.utils.book_append_sheet(wb, ws2, "Danh sch NV")

    // Xut ra base64
    const buffer = XLSX.write(wb, { type: "base64", bookType: "xlsx" })

    return { success: true, data: buffer }
  } catch (error) {
    console.error("Error generating template:", error)
    return {
      success: false,
      error: error instanceof Error ? error.message : "Unknown error",
    }
  }
}
