"use client"

import { useState, useRef } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Badge } from "@/components/ui/badge"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import {
  Tooltip,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from "@/components/ui/tooltip"
import {
  importAttendanceFromExcel,
  generateAttendanceTemplate,
} from "@/lib/actions/attendance-import-actions"
import type { AttendanceLogWithRelations, WorkShift } from "@/lib/types/database"
import { formatDateVN, formatTimeVN, formatSourceVN } from "@/lib/utils/date-utils"
import { Upload, Download, FileSpreadsheet, CheckCircle, XCircle, AlertCircle, AlertTriangle, Clock } from "lucide-react"

interface AttendanceManagementPanelProps {
  attendanceLogs: (AttendanceLogWithRelations & { employee?: { shift?: WorkShift | null } | null })[]
}

interface ImportResult {
  success: boolean
  total: number
  imported: number
  skipped: number
  errors: string[]
}

interface AttendanceViolation {
  type: "late" | "early_leave" | "no_checkin" | "no_checkout"
  minutes?: number
  message: string
}

const months = [
  { value: "01", label: "Thng 1" },
  { value: "02", label: "Thng 2" },
  { value: "03", label: "Thng 3" },
  { value: "04", label: "Thng 4" },
  { value: "05", label: "Thng 5" },
  { value: "06", label: "Thng 6" },
  { value: "07", label: "Thng 7" },
  { value: "08", label: "Thng 8" },
  { value: "09", label: "Thng 9" },
  { value: "10", label: "Thng 10" },
  { value: "11", label: "Thng 11" },
  { value: "12", label: "Thng 12" },
]

// Hm kim tra vi phm chm cng
function checkViolations(
  checkIn: string | null,
  checkOut: string | null,
  shift: WorkShift | null | undefined
): AttendanceViolation[] {
  const violations: AttendanceViolation[] = []

  if (!shift) return violations

  // Parse shift times
  const shiftStart = shift.start_time?.slice(0, 5) // "08:00"
  const shiftEnd = shift.end_time?.slice(0, 5) // "17:00"

  if (!shiftStart || !shiftEnd) return violations

  // Check late arrival
  if (checkIn) {
    const checkInTime = new Date(checkIn)
    const checkInHHMM = `${String(checkInTime.getHours()).padStart(2, "0")}:${String(checkInTime.getMinutes()).padStart(2, "0")}`

    const [shiftH, shiftM] = shiftStart.split(":").map(Number)
    const [checkH, checkM] = checkInHHMM.split(":").map(Number)

    const shiftMinutes = shiftH * 60 + shiftM
    const checkMinutes = checkH * 60 + checkM
    const lateMinutes = checkMinutes - shiftMinutes

    if (lateMinutes > 0) {
      violations.push({
        type: "late",
        minutes: lateMinutes,
        message: `i mun ${lateMinutes} pht (vo lc ${checkInHHMM}, ca bt u ${shiftStart})`,
      })
    }
  } else {
    violations.push({
      type: "no_checkin",
      message: "Qun chm cng vo",
    })
  }

  // Check early leave
  if (checkOut) {
    const checkOutTime = new Date(checkOut)
    const checkOutHHMM = `${String(checkOutTime.getHours()).padStart(2, "0")}:${String(checkOutTime.getMinutes()).padStart(2, "0")}`

    const [shiftH, shiftM] = shiftEnd.split(":").map(Number)
    const [checkH, checkM] = checkOutHHMM.split(":").map(Number)

    const shiftMinutes = shiftH * 60 + shiftM
    const checkMinutes = checkH * 60 + checkM
    const earlyMinutes = shiftMinutes - checkMinutes

    if (earlyMinutes > 0) {
      violations.push({
        type: "early_leave",
        minutes: earlyMinutes,
        message: `V sm ${earlyMinutes} pht (ra lc ${checkOutHHMM}, ca kt thc ${shiftEnd})`,
      })
    }
  } else if (checkIn) {
    // C check in nhng khng c check out
    violations.push({
      type: "no_checkout",
      message: "Qun chm cng ra",
    })
  }

  return violations
}

export function AttendanceManagementPanel({ attendanceLogs }: AttendanceManagementPanelProps) {
  const currentDate = new Date()
  const [importing, setImporting] = useState(false)
  const [downloading, setDownloading] = useState(false)
  const [importResult, setImportResult] = useState<ImportResult | null>(null)
  const [showResultDialog, setShowResultDialog] = useState(false)
  const [showImportDialog, setShowImportDialog] = useState(false)
  const [selectedMonth, setSelectedMonth] = useState(String(currentDate.getMonth() + 1).padStart(2, "0"))
  const [selectedYear, setSelectedYear] = useState(String(currentDate.getFullYear()))
  const [selectedFile, setSelectedFile] = useState<File | null>(null)
  const fileInputRef = useRef<HTMLInputElement>(null)

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (file) {
      // Validate file type
      const validTypes = [
        "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
        "application/vnd.ms-excel",
      ]
      if (!validTypes.includes(file.type) && !file.name.endsWith(".xlsx") && !file.name.endsWith(".xls")) {
        setImportResult({
          success: false,
          total: 0,
          imported: 0,
          skipped: 0,
          errors: ["Ch h tr file Excel (.xlsx, .xls)"],
        })
        setShowResultDialog(true)
        return
      }
      setSelectedFile(file)
      setShowImportDialog(true)
    }
  }

  const handleImport = async () => {
    if (!selectedFile) return

    setImporting(true)
    const formData = new FormData()
    formData.append("file", selectedFile)
    formData.append("monthYear", `${selectedYear}-${selectedMonth}`)

    try {
      const result = await importAttendanceFromExcel(formData)
      setImportResult(result)
      setShowImportDialog(false)
      setShowResultDialog(true)
    } catch (error) {
      setImportResult({
        success: false,
        total: 0,
        imported: 0,
        skipped: 0,
        errors: ["Li khng xc nh khi import"],
      })
      setShowImportDialog(false)
      setShowResultDialog(true)
    } finally {
      setImporting(false)
      setSelectedFile(null)
      if (fileInputRef.current) {
        fileInputRef.current.value = ""
      }
    }
  }

  const handleDownloadTemplate = async () => {
    setDownloading(true)
    try {
      const result = await generateAttendanceTemplate()
      if (result.success && result.data) {
        const byteCharacters = atob(result.data)
        const byteNumbers = new Array(byteCharacters.length)
        for (let i = 0; i < byteCharacters.length; i++) {
          byteNumbers[i] = byteCharacters.charCodeAt(i)
        }
        const byteArray = new Uint8Array(byteNumbers)
        const blob = new Blob([byteArray], {
          type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
        })

        const url = URL.createObjectURL(blob)
        const a = document.createElement("a")
        a.href = url
        a.download = "mau_cham_cong.xlsx"
        document.body.appendChild(a)
        a.click()
        document.body.removeChild(a)
        URL.revokeObjectURL(url)
      }
    } catch (error) {
      console.error("Error downloading template:", error)
    } finally {
      setDownloading(false)
    }
  }

  return (
    <div className="space-y-6">
      {/* Import Section */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <FileSpreadsheet className="h-5 w-5" />
            Import chm cng t Excel
          </CardTitle>
          <CardDescription>
            Upload file Excel  import d liu chm cng hng lot
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex flex-col sm:flex-row gap-4">
            <div className="flex-1">
              <Input
                ref={fileInputRef}
                type="file"
                accept=".xlsx,.xls"
                onChange={handleFileChange}
                disabled={importing}
                className="cursor-pointer"
              />
              <p className="text-sm text-muted-foreground mt-2">
                H tr file .xlsx, .xls
              </p>
            </div>
            <div className="flex gap-2">
              <Button
                variant="outline"
                onClick={handleDownloadTemplate}
                disabled={downloading}
                className="gap-2"
              >
                <Download className="h-4 w-4" />
                {downloading ? "ang ti..." : "Ti file mu"}
              </Button>
            </div>
          </div>

          {/* Format guide */}
          <div className="mt-4 p-4 bg-muted/50 rounded-lg">
            <h4 className="font-medium mb-2">nh dng file Excel:</h4>
            <div className="overflow-x-auto">
              <table className="text-sm border-collapse w-full">
                <thead>
                  <tr className="border-b">
                    <th className="px-3 py-1 text-left">M N.Vin</th>
                    <th className="px-3 py-1 text-left">Tn nhn vin</th>
                    <th className="px-3 py-1 text-left">Phng ban</th>
                    <th className="px-3 py-1 text-left">Ngy</th>
                    <th className="px-3 py-1 text-left">Th</th>
                    <th className="px-3 py-1 text-left">Vo</th>
                    <th className="px-3 py-1 text-left">Ra</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td className="px-3 py-1">NV001</td>
                    <td className="px-3 py-1">Nguyn Vn A</td>
                    <td className="px-3 py-1">IT</td>
                    <td className="px-3 py-1">1</td>
                    <td className="px-3 py-1">T2</td>
                    <td className="px-3 py-1">08:30</td>
                    <td className="px-3 py-1">17:30</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <p className="text-xs text-muted-foreground mt-2">
              * Ct "Ngy" l ngy trong thng (1-31). Thng/nm s chn khi import.
            </p>
          </div>
        </CardContent>
      </Card>

      {/* Attendance List */}
      <Card>
        <CardHeader>
          <CardTitle>D liu chm cng gn y</CardTitle>
          <CardDescription>100 bn ghi mi nht - Vi phm c nh du mu </CardDescription>
        </CardHeader>
        <CardContent>
          <TooltipProvider>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Nhn vin</TableHead>
                  <TableHead>Ngy</TableHead>
                  <TableHead>Gi vo</TableHead>
                  <TableHead>Gi ra</TableHead>
                  <TableHead>Ca lm</TableHead>
                  <TableHead>Ngun</TableHead>
                  <TableHead>Trng thi</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {attendanceLogs.length === 0 ? (
                  <TableRow>
                    <TableCell colSpan={7} className="text-center text-muted-foreground">
                      Cha c d liu chm cng
                    </TableCell>
                  </TableRow>
                ) : (
                  attendanceLogs.map((log) => {
                    const shift = log.employee?.shift
                    const violations = checkViolations(log.check_in, log.check_out, shift)
                    const hasViolation = violations.length > 0
                    const isLate = violations.some((v) => v.type === "late")
                    const isEarlyLeave = violations.some((v) => v.type === "early_leave")
                    const noCheckIn = violations.some((v) => v.type === "no_checkin")
                    const noCheckOut = violations.some((v) => v.type === "no_checkout")

                    return (
                      <TableRow key={log.id} className={hasViolation ? "bg-red-50" : ""}>
                        <TableCell>
                          <div>
                            <div className="font-medium">{log.employee?.full_name}</div>
                            <div className="text-sm text-muted-foreground">
                              {log.employee?.employee_code}
                            </div>
                          </div>
                        </TableCell>
                        <TableCell>{formatDateVN(log.check_in)}</TableCell>
                        <TableCell>
                          {noCheckIn ? (
                            <Tooltip>
                              <TooltipTrigger>
                                <Badge variant="destructive" className="gap-1">
                                  <AlertTriangle className="h-3 w-3" />
                                  Thiu
                                </Badge>
                              </TooltipTrigger>
                              <TooltipContent>Qun chm cng vo</TooltipContent>
                            </Tooltip>
                          ) : isLate ? (
                            <Tooltip>
                              <TooltipTrigger>
                                <span className="text-red-600 font-medium">
                                  {formatTimeVN(log.check_in)}
                                  <AlertTriangle className="h-3 w-3 inline ml-1" />
                                </span>
                              </TooltipTrigger>
                              <TooltipContent>
                                {violations.find((v) => v.type === "late")?.message}
                              </TooltipContent>
                            </Tooltip>
                          ) : (
                            <span className="text-green-600">{formatTimeVN(log.check_in)}</span>
                          )}
                        </TableCell>
                        <TableCell>
                          {noCheckOut ? (
                            <Tooltip>
                              <TooltipTrigger>
                                <Badge variant="destructive" className="gap-1">
                                  <AlertTriangle className="h-3 w-3" />
                                  Thiu
                                </Badge>
                              </TooltipTrigger>
                              <TooltipContent>Qun chm cng ra</TooltipContent>
                            </Tooltip>
                          ) : isEarlyLeave ? (
                            <Tooltip>
                              <TooltipTrigger>
                                <span className="text-red-600 font-medium">
                                  {formatTimeVN(log.check_out)}
                                  <AlertTriangle className="h-3 w-3 inline ml-1" />
                                </span>
                              </TooltipTrigger>
                              <TooltipContent>
                                {violations.find((v) => v.type === "early_leave")?.message}
                              </TooltipContent>
                            </Tooltip>
                          ) : log.check_out ? (
                            <span className="text-green-600">{formatTimeVN(log.check_out)}</span>
                          ) : (
                            <span className="text-muted-foreground">-</span>
                          )}
                        </TableCell>
                        <TableCell>
                          {shift ? (
                            <Badge variant="outline" className="font-mono text-xs">
                              <Clock className="h-3 w-3 mr-1" />
                              {shift.start_time?.slice(0, 5)}-{shift.end_time?.slice(0, 5)}
                            </Badge>
                          ) : (
                            <span className="text-muted-foreground text-xs">Cha c ca</span>
                          )}
                        </TableCell>
                        <TableCell>
                          <Badge variant="outline">{formatSourceVN(log.source)}</Badge>
                        </TableCell>
                        <TableCell>
                          {hasViolation ? (
                            <Tooltip>
                              <TooltipTrigger>
                                <Badge variant="destructive" className="gap-1">
                                  <AlertTriangle className="h-3 w-3" />
                                  Vi phm
                                </Badge>
                              </TooltipTrigger>
                              <TooltipContent>
                                <ul className="text-xs">
                                  {violations.map((v, i) => (
                                    <li key={i}> {v.message}</li>
                                  ))}
                                </ul>
                              </TooltipContent>
                            </Tooltip>
                          ) : log.check_out ? (
                            <Badge className="bg-green-100 text-green-800">Hon thnh</Badge>
                          ) : (
                            <Badge variant="secondary">Cha ra</Badge>
                          )}
                        </TableCell>
                      </TableRow>
                    )
                  })
                )}
              </TableBody>
            </Table>
          </TooltipProvider>
        </CardContent>
      </Card>

      {/* Import Dialog - Chn thng/nm */}
      <Dialog open={showImportDialog} onOpenChange={setShowImportDialog}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle className="flex items-center gap-2">
              <Upload className="h-5 w-5" />
              Import chm cng
            </DialogTitle>
            <DialogDescription>
              Chn thng/nm cho d liu chm cng trong file
            </DialogDescription>
          </DialogHeader>

          <div className="space-y-4 py-4">
            <div className="p-3 bg-muted/50 rounded-lg">
              <p className="text-sm">
                <span className="font-medium">File:</span> {selectedFile?.name}
              </p>
            </div>

            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label>Thng</Label>
                <Select value={selectedMonth} onValueChange={setSelectedMonth}>
                  <SelectTrigger>
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    {months.map((m) => (
                      <SelectItem key={m.value} value={m.value}>
                        {m.label}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              <div className="space-y-2">
                <Label>Nm</Label>
                <Input
                  type="number"
                  value={selectedYear}
                  onChange={(e) => setSelectedYear(e.target.value)}
                  min={2020}
                  max={2100}
                />
              </div>
            </div>
          </div>

          <DialogFooter>
            <Button variant="outline" onClick={() => setShowImportDialog(false)}>
              Hy
            </Button>
            <Button onClick={handleImport} disabled={importing} className="gap-2">
              <Upload className="h-4 w-4" />
              {importing ? "ang import..." : "Import"}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

      {/* Import Result Dialog */}
      <Dialog open={showResultDialog} onOpenChange={setShowResultDialog}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle className="flex items-center gap-2">
              {importResult?.success ? (
                <>
                  <CheckCircle className="h-5 w-5 text-green-500" />
                  Import hon tt
                </>
              ) : (
                <>
                  <XCircle className="h-5 w-5 text-red-500" />
                  Import tht bi
                </>
              )}
            </DialogTitle>
            <DialogDescription>Kt qu import d liu chm cng</DialogDescription>
          </DialogHeader>

          {importResult && (
            <div className="space-y-4">
              <div className="grid grid-cols-3 gap-4">
                <div className="p-3 bg-muted/50 rounded-lg text-center">
                  <p className="text-2xl font-bold">{importResult.total}</p>
                  <p className="text-sm text-muted-foreground">Tng dng</p>
                </div>
                <div className="p-3 bg-green-50 rounded-lg text-center">
                  <p className="text-2xl font-bold text-green-600">{importResult.imported}</p>
                  <p className="text-sm text-muted-foreground">Thnh cng</p>
                </div>
                <div className="p-3 bg-red-50 rounded-lg text-center">
                  <p className="text-2xl font-bold text-red-600">{importResult.skipped}</p>
                  <p className="text-sm text-muted-foreground">B qua</p>
                </div>
              </div>

              {importResult.errors.length > 0 && (
                <div className="p-3 bg-red-50 rounded-lg">
                  <div className="flex items-center gap-2 mb-2">
                    <AlertCircle className="h-4 w-4 text-red-500" />
                    <span className="font-medium text-red-700">Li:</span>
                  </div>
                  <ul className="text-sm text-red-600 space-y-1 max-h-40 overflow-y-auto">
                    {importResult.errors.map((error, i) => (
                      <li key={i}> {error}</li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
          )}

          <DialogFooter>
            <Button onClick={() => setShowResultDialog(false)}>ng</Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  )
}
